import React, { useEffect, useState } from 'react';

// IMPORT DASHBOARD CSS FILE
import './UI.css';

// IMPORT SUMMERY IMAGES
import stdAdmissions from '../../assets/images/icons8-students-48.png';
import siteViewers from '../../assets/images/icons8-vision-48.png';
import appSponsers from '../../assets/images/icons8-helping-hand-48.png';

import ReactTooltip from 'react-tooltip';

const UI = () => {

    const [ Summery, setSummery ] = useState([]);
    const [ MonthlyIncome, setMonthlyIncome ] = useState([]);

    const d = new Date();
    // MONTH NAMES
    const month = ["Jan","Feb","Mar","Apr","May","June","July","Aug","Sept","Oct","Nov","Dec"];
    let currentMonth = month[d.getMonth()];

    useEffect(
        () => {

            setSummery(
                [
                    {
                        img: stdAdmissions, // SUMMERY IMAGE
                        title: 'Total Admission', // SUMMERY ITEM TITLE
                        display: '1200' // SUMMERY DISPLAY
                    },
                    {
                        img: siteViewers, // SUMMERY IMAGE
                        title: 'Website Viewers', // SUMMERY ITEM TITLE
                        display: '300000' // SUMMERY DISPLAY
                    },
                    {
                        img: appSponsers, // SUMMERY IMAGE
                        title: 'Our Sponsers', // SUMMERY ITEM TITLE
                        display: '100' // SUMMERY DISPLAY
                    },
                    {
                        img: appSponsers, // SUMMERY IMAGE
                        title: 'Other', // SUMMERY ITEM TITLE
                        display: '10000' // SUMMERY DISPLAY
                    }
                ]
            );

            setMonthlyIncome(
                [
                    {
                        month: 'Jan',
                        fees: 15000
                    },
                    {
                        month: 'Feb',
                        fees: 45000
                    },
                    {
                        month: 'Mar',
                        fees: 21000
                    },
                    {
                        month: 'Apr',
                        fees: 21500
                    },
                    {
                        month: 'May',
                        fees: 40000
                    },
                    {
                        month: 'June',
                        fees: 22000
                    },
                    {
                        month: 'July',
                        fees: 22100
                    },
                    {
                        month: 'Aug',
                        fees: 22500
                    },
                    {
                        month: 'Sept',
                        fees: 30000
                    },
                    {
                        month: 'Oct',
                        fees: 22500
                    },
                    {
                        month: 'Nov',
                        fees: 22800
                    },
                    {
                        month: 'Dec',
                        fees: 50000
                    }
                ]
            )

            setInterval(() => {
                ReactTooltip.rebuild();
            }, 1000);

        }, []
    )

    return (
        <div className="Home">

            {/* APPLICATION SUMMERY */}
            <div className="summery">
                {
                    Summery.map(
                        ( val, index ) => {
                            return (
                                <div key={ index }>
                                    <div className="Part">
                                        <div>
                                            <img src={ val.img } className="mr-2" width='30' height='30' alt="summery images" />
                                        </div>
                                        <div>
                                            <p className="mb-0 mt-1">{ val.title }</p>
                                            <h5> { val.display } </h5>
                                        </div>
                                    </div>
                                </div>
                            )

                        }
                    )
                }
            </div>

            {/* MONTHLY INCOME */}
            <ReactTooltip id="MonthlyIncome" />
            <div className="ApplicationIncome">
                <div className="Income">
                    <h5>Monthly Income</h5>
                    <h3>$<span>15000</span> </h3>

                    {/* INCOME GRAPH */}
                    <div className="incomeGraph">
                        {
                            MonthlyIncome.map(
                                ( val, index ) => {

                                    let totalIncome = 0;
                                    for ( let x= 0; x < MonthlyIncome.length; x++ )
                                    {

                                        totalIncome = totalIncome + MonthlyIncome[x].fees;

                                    }

                                    let percent = ( val.fees / totalIncome ) * 100;

                                    return (
                                        <div key={ index }>
                                            <div 
                                                className={ currentMonth === val.month ? "GraphItem GraphItemCurrMonth" : "GraphItem" } 
                                                data-for='MonthlyIncome' 
                                                data-tip={ '$ ' + val.fees }
                                                style={
                                                    {
                                                        height: percent + 'vh'
                                                    }
                                                }
                                            ></div>
                                            <p> { val.month } </p>
                                        </div>
                                    )

                                }
                            )
                        }
                    </div>
                </div>

                {/* STUDENTS REPORTS */}
                <div className="studentReport">
                    <label>NEW</label>
                    <h6 className="font-weight-bold" style={ { fontFamily: 'Quicksand', fontSize: '18px' } }>
                        GET STUDENT'S MONTHLY REPORTS WITH NEW FEATURES
                    </h6>
                    <p>
                        New features has been added to student's monthly reports for there parents or guardians
                    </p>
                    <button>VIEW NOW</button>
                </div>
            </div>
        </div>
    )

}

export default UI;